"use client";

import { useEffect, useState } from "react";
import { Auth } from "../components/Auth";
import { Calendar } from "../components/Calendar";

const Page = () => {
  const [session, setSession] = useState<boolean>(false); // now just a boolean
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Simulate checking login from localStorage or API
    const timer = setTimeout(() => {
      const userLoggedIn = false; // mock value — change to true to simulate a login
      setSession(userLoggedIn);
      setLoading(false);
    }, 1000);

    return () => clearTimeout(timer);
  }, []);

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-background">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mx-auto"></div>
          <p className="mt-4 text-muted-foreground">Loading...</p>
        </div>
      </div>
    );
  }

  if (!session) {
    return <Auth />;
  }

  return <Calendar />;
};

export default Page;
